export default "import {\n  CornerstoneImage,\n  CornerstoneSingleImage,\n  HeatmapViewer,\n  InsightViewer,\n  installWADOImageLoader,\n  unloadImage,\n  useInsightViewerSync,\n} from '@lunit/insight-viewer';\nimport React, { useMemo } from 'react';\nimport data from '../../../__fixtures__/posMap.sample.json';\n\ninstallWADOImageLoader();\n\nexport default () => {\n  const image: CornerstoneImage = useMemo(\n    () =>\n      new CornerstoneSingleImage(`wadouri:https://fixtures.front.lunit.io/dcm-files/series/CT000010.dcm`, {\n        unload: unloadImage,\n      }),\n    [],\n  );\n\n  const { cornerstoneRenderData, updateCornerstoneRenderData } = useInsightViewerSync();\n\n  const resetTime = useMemo<number>(() => Date.now(), []);\n\n  return (\n    <div style={{ display: 'flex' }}>\n      <InsightViewer\n        width={400}\n        height={500}\n        invert={false}\n        flip={false}\n        pan={true}\n        adjust={false}\n        zoom={true}\n        resetTime={resetTime}\n        image={image}\n        updateCornerstoneRenderData={updateCornerstoneRenderData} // 받아서\n      />\n      <div style={{ position: 'relative', width: 400, height: 500, backgroundColor: 'navy' }}>\n        <HeatmapViewer\n          width={400}\n          height={500}\n          posMap={data.engine_result.engine_result.pos_map}\n          threshold={0.1}\n          cornerstoneRenderData={cornerstoneRenderData} // 넣어준다\n        />\n      </div>\n    </div>\n  );\n};\n"